import '../../../App.css';
import SlideInicial from '../../../ImdCommons/SlideInicial';
import { Box, List, makeStyles, Typography } from '@material-ui/core';
import { themeBaseStyles } from '../../../styles';
import ImdSlideDuvidas from '../../../ImdCommons/ImdSlideDuvidas';
import { useEffect } from 'react';
import SlideAgenda, { Topico } from '../../../ImdCommons/SlideAgenda';
import ImdSlide from '../../../ImdCommons/ImdSlide';
import Math from '../../../ImdCommons/Math';
import { ArvoresBuscaBinaria } from '../aula-07/Arvores';
import { arvoreGraduada, arvoreRubroNegra, arvoreRubroNegraSemCor } from '../../../data/arvoreRubroNegra';
import rnSimplesDireita from '../../../img/rnSimplesDireita.PNG';
import rnDuplaDireita from '../../../img/rnDuplaDireita.PNG';
import rnSimplesEsquerda from '../../../img/rnSimplesEsquerda.PNG';
import rnDuplaEsquerda from '../../../img/rnDuplaEsquerda.PNG';
import rnDefinicoes from '../../../img/rnDefinicoes.PNG';
import rnDefinicoesCenario2 from '../../../img/rnDefinicoesCenario2.png';
import rnDefinicoesTriangulo from '../../../img/rnDefinicoesTriangulo.PNG';

const useStyles = makeStyles((theme, props) => (themeBaseStyles(theme, props)));

export function SlidesAula14EDB2(props) {
  const classes = useStyles(props);

  useEffect(() => {
    document.title = "EDB2 - Aula 13 - rvores B (B-Tree) e digitais (Tries)"
  }, []);

  return (
    <>
      <SlideInicial
        codigoDisciplina="IMD0039"
        nomeDisciplina="Estruturas de Dados Bsicas 2"
        assunto="rvores B (B-Tree) e digitais (Tries)"
        semestre="2020.2"
        />
      <SlideAgenda>
        <Topico>rvores B (B-Tree)</Topico>
        <Topico>rvores Digitais</Topico>
        <Topico>Implementao de rvore digital</Topico>
      </SlideAgenda>
      <ImdSlide heading="rvore B (B-Tree)">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="left"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>Mais de uma chave  armazenada em cada n.</Typography>
          <Typography className={classes.topicoS2}>Assegura que todas as suas folhas estejam no ltimo nvel da rvore.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvore B (B-Tree): ordem">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="left"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>Diz-se que uma rvore B  de <span className={classes.emphasis}>ordem <Math tex={String.raw`d`}/></span> se ela  vazia ou satisfaz as condies:</Typography>
          <Typography className={classes.topicoS2}>(i) A raiz  uma folha ou tem pelo menos dois filhos.</Typography>
          <Typography className={classes.topicoS2}>(ii) Cada n diferente das folhas e da raiz tem pelo menos <Math tex={String.raw`d + 1`}/> filhos.</Typography>
          <Typography className={classes.topicoS2}>(iii) Cada n tem no mximo <Math tex={String.raw`2d + 1`}/> filhos.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvore B (B-Tree): pgina">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="left"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>Um n de uma rvore B  chamado de PGINA e apresenta as seguintes propriedades:</Typography>
          <Typography className={classes.topicoS2}>(i) Seja <Math tex={String.raw`m`}/> o nmero de chaves em uma pgina P no folha, ento P possui <Math tex={String.raw`m + 1`}/> filhos.</Typography>
          <Typography className={classes.topicoS2}>(ii) Cada pgina possui entre <Math tex={String.raw`d`}/> e <Math tex={String.raw`2d`}/> chaves, exceto o n raiz que possui entre <Math tex={String.raw`1`}/> e <Math tex={String.raw`2d`}/> chaves.</Typography>
          <Typography className={classes.topicoS2}>(iii) As chaves de cada pgina esto ordenadas e <Math tex={String.raw`d \leqslant m \leqslant 2d`}/>. Para a raiz, <Math tex={String.raw`1 \leqslant m \leqslant 2d`}/>.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvore B (B-Tree): exemplo">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="left"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>Adicionar os ns 9, 30, 60, 80 no link abaixo:</Typography>
          <Typography className={classes.topicoS2}><a href="https://www.cs.usfca.edu/~galles/visualization/BTree.html" target="_blank" rel="noreferrer">https://www.cs.usfca.edu/~galles/visualization/BTree.html</a></Typography>
          <Typography className={classes.topicoS2}>O que acontece se inserirmos o 70?</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvore B (B-Tree): busca">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="left"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>Semelhante a uma busca em uma rvore binria de busca.</Typography>
          <Typography className={classes.topicoS2}>Os filhos so sempre maiores que o n esquerdo do pai e menores que o n direito do pai.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvore B (B-Tree): insero">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="left"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>Encontra o n folha onde o novo elemento deve ser inserido.</Typography>
          <Typography className={classes.topicoS2}>Insere o novo elemento.</Typography>
          <Typography className={classes.topicoS2}>Caso o n passe a ter mais chaves do que o limite, divide o n em dois e coloca o elemento do meio no n pai.</Typography>
          <Typography className={classes.topicoS2}>Este procedimento pode se repetir at que seja necessrio dividir a raiz e com isso aumentar a altura da rvore.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvore B (B-Tree): remoo">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="left"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>Encontra o n a ser removido.</Typography>
          <Typography className={classes.topicoS2}>Susbtitui ele pelo seu maior n de sua sub-rvore esquerda ou pelo menor n de sua sub-rvore direita.</Typography>
          <Typography className={classes.topicoS2}>Remove o elemento de seu n.</Typography>
          <Typography className={classes.topicoS2}>Caso alguma regra seja violada, pode ocorrer a fuso de dois ns.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvore B (B-Tree): complexidades">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="left"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>Busca: <Math tex={String.raw`O(\log{n})`}/></Typography>
          <Typography className={classes.topicoS2}>Insero: <Math tex={String.raw`O(\log{n})`}/></Typography>
          <Typography className={classes.topicoS2}>Remoo: <Math tex={String.raw`O(\log{n})`}/></Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores digitais (Tries)">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="left"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>Considere um alfabeto formado por dgitos <Math tex={String.raw`d_1, ... d_n`}/>.</Typography>
          <Typography className={classes.topicoS2}>Cada chave  formada por uma sequncia de dgitos.</Typography>
          <Typography className={classes.topicoS2}>A chave no  considerada atmica.</Typography>
          <Typography className={classes.topicoS2}>A busca  feita dgito a dgito.</Typography>
          <Typography className={classes.topicoS2}>O n final da chave armazena o seu valor.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores digitais (Tries): definio">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="left"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>Uma rvore digital para S  uma rvore m-ria T, no vazia, tal que:</Typography>
          <Typography className={classes.topicoS2}>(i) Seja v um n de T, o j-simo filho de seu pai, ento v corresponde ao dgito dj do alfabeto de S, <Math tex={String.raw`1 \leqslant j \leqslant m`}/>.</Typography>
          <Typography className={classes.topicoS2}>(ii) Para cada n v, a sequncia de dgitos definida pelo caminho desde a raiz de T a v corresponde a um prefixo de alguma chave de S.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores digitais (Tries): busca">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="left"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>Navega na rvore dgito a dgito da chave.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores digitais (Tries): insero">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="left"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>Navega na rvore dgito a dgito da chave.</Typography>
          <Typography className={classes.topicoS2}>Caso o n no exista, ele  criado.</Typography>
          <Typography className={classes.topicoS2}>O valor da chave  atribudo a ltimo n dela.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores digitais (Tries): remoo">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="left"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>Navega na rvore dgito a dgito da chave.</Typography>
          <Typography className={classes.topicoS2}>Caso encontre valor para a chave, remove o valor.</Typography>
          <Typography className={classes.topicoS2}>Se o n no tive filhos, remove todos os ns  partir de um n pai que contenha valor.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores digitais (Tries): exemplo">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="left"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}><a href="https://www.cs.usfca.edu/~galles/visualization/Trie.html" target="_blank" rel="noreferrer">https://www.cs.usfca.edu/~galles/visualization/Trie.html</a></Typography>
        </Box>
      </ImdSlide>
      
      <ImdSlideDuvidas/>
      
      <ImdSlide heading="Referncias">
        <Box className={classes.slideContentColumnBox}>
          <List component="ul" className={classes.numbersOl}>
            <Topico>SZWARCFITER, Jayme Luiz; MARKENZON, Lilian. Estruturas de dados e seus algoritmos. 3. ed. Rio de Janeiro: LTC, 2010.</Topico>
          </List>
        </Box>
      </ImdSlide>
    </>
  );
}
