import '../../../App.css';
import SlideInicial from '../../../ImdCommons/SlideInicial';
import { Box, List, makeStyles, Typography } from '@material-ui/core';
import { themeBaseStyles } from '../../../styles';
import ImdSlideDuvidas from '../../../ImdCommons/ImdSlideDuvidas';
import { useEffect } from 'react';
import SlideAgenda, { Topico } from '../../../ImdCommons/SlideAgenda';
import ImdSlide from '../../../ImdCommons/ImdSlide';
import Math from '../../../ImdCommons/Math';
import { ArvoresBuscaBinaria } from '../aula-07/Arvores';
import { arvoreGraduada, arvoreRubroNegra, arvoreRubroNegraSemCor } from '../../../data/arvoreRubroNegra';
import rnSimplesDireita from '../../../img/rnSimplesDireita.PNG';
import rnDuplaDireita from '../../../img/rnDuplaDireita.PNG';
import rnSimplesEsquerda from '../../../img/rnSimplesEsquerda.PNG';
import rnDuplaEsquerda from '../../../img/rnDuplaEsquerda.PNG';
import rnDefinicoes from '../../../img/rnDefinicoes.PNG';
import rnDefinicoesCenario2 from '../../../img/rnDefinicoesCenario2.png';
import rnDefinicoesTriangulo from '../../../img/rnDefinicoesTriangulo.PNG';

const useStyles = makeStyles((theme, props) => (themeBaseStyles(theme, props)));

export function SlidesAula13EDB2(props) {
  const classes = useStyles(props);

  useEffect(() => {
    document.title = "EDB2 - Aula 13 - rvore Rubro-Negra"
  }, []);

  return (
    <>
      <SlideInicial
        codigoDisciplina="IMD0039"
        nomeDisciplina="Estruturas de Dados Bsicas 2"
        assunto="rvore Rubro-Negra"
        semestre="2020.2"
        />
      <SlideAgenda>
        <Topico>rvores Graduadas</Topico>
        <Topico>rvores Rubro-Negras</Topico>
        <Topico>Operaes em rvores Rubro-Negras</Topico>
      </SlideAgenda>
      <ImdSlide heading="rvore Graduadas">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="left"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>Uma <span className={classes.emphasis}>rvore graduada</span>  uma rvore binria de busca.</Typography>
          <Typography className={classes.topicoS2}>Para cada n v da rvore, existe um nmero inteiro chamado de <span className={classes.emphasis}>posto(v)</span>.</Typography>
          <Typography className={classes.topicoS2}>Se v  um n externo, ento <Math tex={String.raw`posto(v) = 0`}/>.</Typography>
          <Typography className={classes.topicoS2}>Se v  pai de um n externo, ento <Math tex={String.raw`posto(v) = 1`}/>.</Typography>
          <Typography className={classes.topicoS2}>Se v possui pai w, ento <Math tex={String.raw`posto(v) \leqslant posto(w) \leqslant posto(v) +  1`}/>.</Typography>
          <Typography className={classes.topicoS2}>Se v possui av w, ento <Math tex={String.raw`posto(v) < posto(w)`}/>.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvore Graduadas: exemplo">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="left"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria arvore={arvoreGraduada} zoom={0.6}/>
          <Typography className={classes.topicoS2}>Esta rvore  AVL?</Typography>
          <Typography className={classes.topicoS2}>Esta rvore  graduada?</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvore Rubro-Negra">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="left"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>Um n pode ser <span className={classes.emphasis}>vermelho</span> ou <span className={classes.emphasis}>preto</span>.</Typography>
          <Typography className={classes.topicoS2}>A <span className={classes.emphasis}>raiz e as folhas vazias</span> so ns <span className={classes.emphasis}>pretos</span>.</Typography>
          <Typography className={classes.topicoS2}>Se um n  <span className={classes.emphasis}>vermelho</span>, ento os seus filhos <span className={classes.emphasis}>so pretos</span>.</Typography>
          <Typography className={classes.topicoS2}>Todos os caminhos  partir de um n at os seus descendentes que so folhas vazias possuem o <span className={classes.emphasis}>mesmo nmero de ns pretos</span>.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvore Rubro-Negra: exemplo">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          
          <ArvoresBuscaBinaria arvore={arvoreRubroNegraSemCor} zoom={0.75}/>
          <Typography className={classes.topicoS2}>Esta rvore  Rubro-Negra?</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="Graduada  Rubro-Negra">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="left"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>O n raiz  sempre preto.</Typography>
          <Typography className={classes.topicoS2}>Se v possui pai w, e <Math tex={String.raw`posto(v) == posto(w)`}/>, ento v  vermelho.</Typography>
          <Typography className={classes.topicoS2}>Seno, v  preto.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="Graduada  Rubro-Negra">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          
          <ArvoresBuscaBinaria arvore={arvoreRubroNegraSemCor} zoom={0.75}/>
          <Typography className={classes.topicoS2}>Esta rvore  Rubro-Negra?</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvore Rubro-Negra: exemplo">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          
          <ArvoresBuscaBinaria arvore={arvoreRubroNegra} zoom={0.75}/>
        </Box>
      </ImdSlide>
      <ImdSlide heading="Rubro-Negra  Graduada">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="left"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>Se o n v  externo, ento <Math tex={String.raw`posto(v) = 0`}/>.</Typography>
          <Typography className={classes.topicoS2}>Se v possui pai w e v  vermelho, ento <Math tex={String.raw`posto(w) = posto(v)`}/>.</Typography>
          <Typography className={classes.topicoS2}>Se v possui pai w e v  preto, ento <Math tex={String.raw`posto(w) = posto(v) + 1`}/>.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="Rubro-Negra  Graduada">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          
          <ArvoresBuscaBinaria arvore={arvoreRubroNegra} zoom={0.75}/>
        </Box>
      </ImdSlide>
      
      <ImdSlide heading="Propriedades">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>O posto de um n em uma rvore Rubro-Negra  a quantidade de ns pretos entre ele e um n externo.</Typography>
          <Typography className={classes.topicoS2}>A altura h de um n v em uma rvore Graduada  tal que <Math tex={String.raw`h(v) \leqslant 2 \cdot posto(v)`}/>.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores Rubro-Negras: operaes">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>Busca.</Typography>
          <Typography className={classes.topicoS2}>Insero.</Typography>
          <Typography className={classes.topicoS2}>Remoo.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores Rubro-Negras: insero">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="left"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>1. Realiza a busca da posio onde o n deve ser inserido.</Typography>
          <Typography className={classes.topicoS2}>2. Marca o n como vermelho.</Typography>
          <Typography className={classes.topicoS2}>3. Verifica a necessidade de rotao ou mudana de cor do n.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores Rubro-Negras: definies">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <img src={rnDefinicoes} alt="rvore Rubro-Negras: definies"/>
          <Typography className={classes.topicoS2}><span className={classes.emphasis}>q</span>  o n inserido; <span className={classes.emphasis}>v</span>  o pai de q; <span className={classes.emphasis}>w</span>  o av de q; <span className={classes.emphasis}>t</span>  o tio de q.</Typography>
        </Box>
      </ImdSlide>
      {/* q = Z, v = A, w = B, t = D */}
      <ImdSlide heading="rvores Rubro-Negras: cenrios">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>1. <span className={classes.emphasis}>q</span>  a raiz.</Typography>
          <Typography className={classes.topicoS2}>2. <span className={classes.emphasis}>t</span>  o tio de q e  vermelho.</Typography>
          <Typography className={classes.topicoS2}>3. <span className={classes.emphasis}>t</span>  o tio de q e  preto e eles formam um tringulo.</Typography>
          <Typography className={classes.topicoS2}>4. <span className={classes.emphasis}>t</span>  o tio de q e  preto e eles formam uma linha.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores Rubro-Negras: cenrio 1">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>1. <span className={classes.emphasis}>q</span>  a raiz.</Typography>
          <Typography className={classes.topicoS2}>Soluo: trocar a cor de q.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores Rubro-Negras: cenrio 2">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>2. <span className={classes.emphasis}>t</span>  o tio de q e  vermelho.</Typography>
          <img src={rnDefinicoesCenario2} alt="rvore Rubro-Negras: cenrio 2"/>
          <Typography className={classes.topicoS2}>Soluo: trocar as cores do pai de q (v), do av de q (w) e do tio de q (t).</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores Rubro-Negras: cenrio 3">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>3. <span className={classes.emphasis}>t</span>  o tio de q e  preto e eles formam um tringulo.</Typography>
          <img src={rnDefinicoesTriangulo} alt="rvore Rubro-Negras: cenrio 3"/>
          <Typography className={classes.topicoS2}>Soluo: rotaciona q para a posio de v para que forme uma linha.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores Rubro-Negras: cenrio 4">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>4. <span className={classes.emphasis}>t</span>  o tio de q e  preto e eles formam uma linha.</Typography>
          <img src={rnDefinicoes} alt="rvore Rubro-Negras: cenrio 4"/>
          <Typography className={classes.topicoS2}>Soluo: rotaciona v para a posio de w e troca as cores de v e w.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores Rubro-Negras: rotaes">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>Rotao simples  direita.</Typography>
          <Typography className={classes.topicoS2}>Rotao simples  esquerda.</Typography>
          <Typography className={classes.topicoS2}>Rotao dupla  direita.</Typography>
          <Typography className={classes.topicoS2}>Rotao dupla  esquerda.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores Rubro-Negras: rotao simples  direita">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <img src={rnSimplesDireita} alt="rvore Rubro-Negras: rotao simples  direita"/>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores Rubro-Negras: rotao dupla  direita">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <img src={rnDuplaDireita} alt="rvore Rubro-Negras: rotao dupla  direita"/>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores Rubro-Negras: rotao simples  esquerda">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <img src={rnSimplesEsquerda} alt="rvore Rubro-Negras: rotao simples  esquerda"/>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores Rubro-Negras: rotao dupla  esquerda">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <img src={rnDuplaEsquerda} alt="rvore Rubro-Negras: rotao dupla  esquerda"/>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores Rubro-Negras: demonstrao">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}><a href="https://www.inf.ufsc.br/~aldo.vw/estruturas/simulador/RB.html" target="_blank" rel="noreferrer">https://www.inf.ufsc.br/~aldo.vw/estruturas/simulador/RB.html</a></Typography>
        </Box>
      </ImdSlide>
      
      <ImdSlideDuvidas/>
      
      <ImdSlide heading="Referncias">
        <Box className={classes.slideContentColumnBox}>
          <List component="ul" className={classes.numbersOl}>
            <Topico>SZWARCFITER, Jayme Luiz; MARKENZON, Lilian. Estruturas de dados e seus algoritmos. 3. ed. Rio de Janeiro: LTC, 2010.</Topico>
          </List>
        </Box>
      </ImdSlide>
    </>
  );
}
