import '../../../App.css';
import SlideInicial from '../../../ImdCommons/SlideInicial';
import { Box, List, makeStyles, Typography } from '@material-ui/core';
import { themeBaseStyles } from '../../../styles';
import ImdSlideDuvidas from '../../../ImdCommons/ImdSlideDuvidas';
import { useEffect } from 'react';
import SlideAgenda, { Topico } from '../../../ImdCommons/SlideAgenda';
import ImdSlide from '../../../ImdCommons/ImdSlide';
import { BuscaBinaria } from '../aula-07/BuscaBinaria';
import { ArvoreBinariaDefinicao, ArvoresBuscaBinaria } from '../aula-07/Arvores';
import Math from '../../../ImdCommons/Math';
import { arvoreBinaria, arvoreBinariaBusca, arvoreBinariaBuscaDesbalanceada, arvoreBinariaBuscaZigueZague, arvoreBinariaBuscaComFolha } from '../../../data/arvoreBinaria';
import { ImdVector } from '../../../ImdCommons/ImdVector';

const useStyles = makeStyles((theme, props) => (themeBaseStyles(theme, props)));

export function SlidesAula09EDB2(props) {
  const classes = useStyles(props);
  const orderedVector = [{value: 8}, {value: 12}, {value: 15}, {value: 18}, {value: 28}, {value: 45}, {value: 62}];

  useEffect(() => {
    document.title = "EDB2 - Aula 09 - rvores Binrias de Busca"
  }, []);

  return (
    <>
      <SlideInicial
        codigoDisciplina="IMD0039"
        nomeDisciplina="Estruturas de Dados Bsicas 2"
        assunto="rvores Binrias de Busca"
        semestre="2020.2"
        />
      <SlideAgenda>
        <Topico>rvore Binria de Busca</Topico>
        <Topico>Operaes e complexidades</Topico>
        <Topico>Caminhos</Topico>
        <Topico>rvores Balanceadas</Topico>
      </SlideAgenda>
      <ImdSlide heading="rvore Binria de Busca">
        <ArvoresBuscaBinaria buscaBinaria={true}/>
      </ImdSlide>
      <ImdSlide heading="rvore Binria: Definio">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="left"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}> uma <span className={classes.emphasis}>rvore binria</span> tal que:</Typography>
          <Typography className={classes.topicoS2}>A raiz possui uma chave</Typography>
          <Typography className={classes.topicoS2}>As chaves dos ns da subrvore <span className={classes.emphasis}>esquerda</span> da raiz so <span className={classes.emphasis}>menores</span> que a chave da raiz</Typography>
          <Typography className={classes.topicoS2}>As chaves dos ns da subrvore <span className={classes.emphasis}>direita</span> da raiz so <span className={classes.emphasis}>maiores</span> que a chave da raiz</Typography>
          <Typography className={classes.topicoS2}>As subrvores esquerda e direita so rvores binrias de busca</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvore Binria de Busca: Exemplo">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria arvore={arvoreBinaria}/>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvore Binria de Busca: Operaes">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="left"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>Busca</Typography>
          <Typography className={classes.topicoS2}>Insero</Typography>
          <Typography className={classes.topicoS2}>Remoo</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvore Binria de Busca: Busca">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>Dada uma <span className={classes.emphasis}>coleo</span> de elementos previamente <span className={classes.emphasis}>ordenada</span></Typography>
          <Typography className={classes.topicoS2}><span className={classes.emphasis}>Encontrar</span> o elemento que contenha uma dada <span className={classes.emphasis}>chave</span></Typography>
          <Typography className={classes.topicoS3}><span className={classes.danger}>Qual algoritmo devemos utilizar?</span></Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvore Binria de Busca: Busca">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ImdVector
            data={orderedVector}
            height={100}  />
          
          <Typography className={classes.topicoS3}><span className={classes.emphasis}>Busca Binria</span></Typography>
          <Typography className={classes.topicoS2}><span className={classes.danger}>Qual a complexidade?</span></Typography>
          <Typography className={classes.topicoS2}><span className={classes.danger}>Como transformamos esta coleo de elementos em uma rvore?</span></Typography>
        </Box>
      </ImdSlide>
      
      <ImdSlide heading="rvore Binria de Busca: Busca">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ImdVector
            data={orderedVector}
            height={100}  />
          <ArvoresBuscaBinaria arvore={arvoreBinariaBusca}/>
          <Typography className={classes.topicoS2}><span className={classes.danger}>Qual a complexidade?</span></Typography>
        </Box>
      </ImdSlide>
      
      <ImdSlide heading="rvore Binria de Busca: Busca">
        <Box display="flex"
            className={classes.root}
            flexDirection="row"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria arvore={arvoreBinariaBuscaDesbalanceada} zoom={0.65}/>
          <Typography className={classes.topicoS2}><span className={classes.danger}>Tambm  uma rvore binria de busca?</span></Typography>
        </Box>
      </ImdSlide>
      
      <ImdSlide heading="rvore Binria de Busca: Busca">
        <Box display="flex"
            className={classes.root}
            flexDirection="row"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria arvore={arvoreBinariaBuscaZigueZague} zoom={0.65}/>
          <Typography className={classes.topicoS2}><span className={classes.danger}>Qual a complexidade?</span></Typography>
        </Box>
      </ImdSlide>
      
      <ImdSlide heading="rvore Binria de Busca: Busca">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria arvore={arvoreBinariaBusca}/>
          <Typography className={classes.topicoS2}>Se a rvore binria de busca for <span className={classes.emphasis}>completa</span>, a complexidade da busca  <span className={classes.emphasis}><Math tex={String.raw`O(\log{n})`}/></span></Typography>
        </Box>
      </ImdSlide>
      
      <ImdSlide heading="rvore Binria de Busca: Insero">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria arvore={arvoreBinariaBusca}/>
          <Typography className={classes.topicoS2}><span className={classes.danger}>Qual o caminho para inserir o elemento 21 e manter as condies da rvore binria de busca?</span></Typography>
          <Typography className={classes.topicoS2}><span className={classes.danger}>Qual a complexidade?</span></Typography>
        </Box>
      </ImdSlide>
      
      <ImdSlide heading="rvore Binria de Busca: Insero">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria arvore={arvoreBinariaBuscaComFolha} zoom={0.75}/>
        </Box>
      </ImdSlide>
      
      <ImdSlide heading="Exerccio">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>Alterar o mtodo de insero na nossa rvore para que ela sempre seja uma rvore binria de busca.</Typography>
          <Typography className={classes.topicoS2}>O que acontece se inserirmos o vetor [{orderedVector.map(el => el.value).join(",")}] iterando sobre ele?</Typography>
        </Box>
      </ImdSlide>
      
      <ImdSlide heading="rvore Binria de Busca: Remoo">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria arvore={arvoreBinariaBuscaComFolha} zoom={0.75}/>
          <Typography className={classes.topicoS2}><span className={classes.danger}>Qual o algoritmo para remover o elemento 8?</span></Typography>
          <Typography className={classes.topicoS2}><span className={classes.danger}>Qual a complexidade?</span></Typography>
        </Box>
      </ImdSlide>
      
      <ImdSlide heading="rvore Binria de Busca: Remoo">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria arvore={arvoreBinariaBuscaComFolha} zoom={0.75}/>
          <Typography className={classes.topicoS2}><span className={classes.danger}>Qual o algoritmo para remover o elemento 28?</span></Typography>
          <Typography className={classes.topicoS2}><span className={classes.danger}>Qual a complexidade?</span></Typography>
        </Box>
      </ImdSlide>
      
      <ImdSlide heading="rvore Binria de Busca: Remoo">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria arvore={arvoreBinariaBuscaComFolha} zoom={0.75}/>
          <Typography className={classes.topicoS2}><span className={classes.danger}>Qual o algoritmo para remover o elemento 18?</span></Typography>
          <Typography className={classes.topicoS2}><span className={classes.danger}>Qual a complexidade?</span></Typography>
        </Box>
      </ImdSlide>

      <ImdSlide heading="Exerccio">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>Alterar o mtodo de remoo da nossa rvore para que ela sempre seja uma rvore binria de busca.</Typography>
        </Box>
      </ImdSlide>

      <ImdSlideDuvidas/>
      
      <ImdSlide heading="Referncias">
        <Box className={classes.slideContentColumnBox}>
          <List component="ul" className={classes.numbersOl}>
            <Topico>SZWARCFITER, Jayme Luiz; MARKENZON, Lilian. Estruturas de dados e seus algoritmos. 3. ed. Rio de Janeiro: LTC, 2010.</Topico>
          </List>
        </Box>
      </ImdSlide>
    </>
  );
}
