import '../../../App.css';
import SlideInicial from '../../../ImdCommons/SlideInicial';
import { Box, List, makeStyles, Typography } from '@material-ui/core';
import { themeBaseStyles } from '../../../styles';
import ImdSlideDuvidas from '../../../ImdCommons/ImdSlideDuvidas';
import { useEffect } from 'react';
import SlideAgenda, { Topico } from '../../../ImdCommons/SlideAgenda';
import ImdSlide from '../../../ImdCommons/ImdSlide';
import { BuscaBinaria } from './BuscaBinaria';
import { ArvoreBinariaDefinicao, ArvoresBuscaBinaria, ArvoresConceitos, ArvoresDefinicao } from './Arvores';
import Math from '../../../ImdCommons/Math';
import { arvoreBinaria, arvoreBinariaCompleta, arvoreEstritamenteBinaria } from '../../../data/arvoreBinaria';

const useStyles = makeStyles((theme, props) => (themeBaseStyles(theme, props)));

export function SlidesAula07EDB2(props) {
  const classes = useStyles(props);

  useEffect(() => {
    document.title = "EDB2 - Aula 07 - Conceitos de rvores"
  }, []);

  return (
    <>
      <SlideInicial
        codigoDisciplina="IMD0039"
        nomeDisciplina="Estruturas de Dados Bsicas 2"
        assunto="rvores"
        semestre="2020.2"
        />
      <SlideAgenda>
        <Topico>Conceito de rvore</Topico>
        <Topico>Limite inferior de um problema</Topico>
        <Topico>rvore de deciso</Topico>
        <Topico>Algoritmo timo</Topico>
      </SlideAgenda>
      <ImdSlide heading="Busca Binria">
        <BuscaBinaria/>
      </ImdSlide>
      <ImdSlide heading="rvores">
        <ArvoresBuscaBinaria buscaBinaria={true}/>
      </ImdSlide>
      <ImdSlide heading="rvores: Definio">
        <ArvoresDefinicao/>
      </ImdSlide>
      <ImdSlide heading="rvores: Conceitos">
        <ArvoresConceitos/>
      </ImdSlide>
      <ImdSlide heading="rvores: Raiz">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria raiz={true}/>
          <Typography className={classes.topicoS2}>A rvore  referenciada  partir de sua raiz, chamada de <span className={classes.emphasis}>r</span>.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores: Subrvore">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria subarvore={true}/>
          <Typography className={classes.topicoS2}>Uma <span className={classes.emphasis}>subrvore de T</span> cuja raiz  um n <span className={classes.emphasis}>v</span>  chamada de <span className={classes.emphasis}>T<sub>v</sub></span>.</Typography>
          <Typography className={classes.topicoS2}>Se E pertence a T<sub>B</sub>, ento E  <span className={classes.emphasis}>descendente</span> de B e B  <span className={classes.emphasis}>ancestral</span> de E.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores: Subrvore">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria subarvore2={true}/>
          <Typography className={classes.topicoS2}>Mesmo os ns que no possuem descendentes tambm so razes de uma subrvore.</Typography>
          <Typography className={classes.topicoS2}><span className={classes.emphasis}>E  raiz da subrvore T<sub>E</sub>.</span></Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores: Folhas">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria folhas={true}/>
          <Typography className={classes.topicoS2}>Os ns que no possuem descendentes so chamados de <span className={classes.emphasis}>folhas</span>.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores: Caminhos">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria caminhos={true}/>
          <Typography className={classes.topicoS2}>A ligao entre dois ns de uma rvore  chamada de <span className={classes.emphasis}>arco</span> ou <span className={classes.emphasis}>aresta</span>.</Typography>
          <Typography className={classes.topicoS2} style={{textAlign: 'center'}}>O <span className={classes.emphasis}>caminho</span> de um n A at um n F  o conjunto de arestas que devem ser percorridas para chegar de A a F.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores: Altura">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria altura={true}/>
          <Typography className={classes.topicoS2}>A altura <span className={classes.emphasis}>h</span> de um n  maior distncia entre ele e uma folha.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores: Nvel">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria nivel={true}/>
          <Typography className={classes.topicoS2}>O nvel de um n  maior distncia entre ele e a raiz.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores: Grau de um n">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria grau={true}/>
          <Typography className={classes.topicoS2}>O <span className={classes.emphasis}>grau</span> de um n  dado pelo nmero de filhos de um n.</Typography>
          <Typography className={classes.topicoS2}>Um n com <Math tex={String.raw`grau > 0`}/>  chamado de <span className={classes.emphasis}>n interior</span>.</Typography>
          <Typography className={classes.topicoS2}><span className={classes.danger}>E um n com <Math tex={String.raw`grau = 0`}/>?</span></Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores: Grau de uma rvore (Aridade)">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria grauArvore={true}/>
          <Typography className={classes.topicoS2}>Uma <span className={classes.emphasis}>rvore m-ria</span>  uma rvore cujos ns tm <Math tex={String.raw`grau <= m`}/>.</Typography>
          <Typography className={classes.topicoS2}>T<sub>B</sub>  uma subrvore <span className={classes.emphasis}>binria</span>.</Typography>
          <Typography className={classes.topicoS2}><span className={classes.danger}>Qual a aridade de T?</span></Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores: Relaes">
        <Box display="flex"
            className={classes.root}
            flexDirection="row"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria relacoes={true}/>
          <Box display="flex"
              className={classes.root}
              flexDirection="row"
              alignItems="center"
              justifyContent="space-evenly">
            <Box display="flex"
                className={classes.root}
                flexDirection="column"
                alignItems="left"
                justifyContent="space-evenly">
              <Typography className={classes.topicoS1}>A  a <span className={classes.emphasis}>raiz</span> da rvore.</Typography>
              <Typography className={classes.topicoS1}>A  <span className={classes.emphasis}>pai</span> de B, C e D.</Typography>
              <Typography className={classes.topicoS1}>A  <span className={classes.emphasis}>av</span> de E, F e G.</Typography>
              <Typography className={classes.topicoS1}>B  <span className={classes.emphasis}>raiz da subrvore</span> <Math tex={String.raw`T_B`}/>.</Typography>
              <Typography className={classes.topicoS1}>B  <span className={classes.emphasis}>filho</span> de A.</Typography>
              <Typography className={classes.topicoS1}>B  <span className={classes.emphasis}>irmo</span> de C e D.</Typography>
            </Box>
            <Box display="flex"
                className={classes.root}
                flexDirection="column"
                alignItems="left"
                justifyContent="space-evenly">
              <Typography className={classes.topicoS1}>B  <span className={classes.emphasis}>pai</span> de E, F e G.</Typography>
              <Typography className={classes.topicoS1}>E, F, G e D so <span className={classes.emphasis}>folhas</span>.</Typography>
              <Typography className={classes.topicoS1}>E  <span className={classes.emphasis}>raiz da subrvore</span> <Math tex={String.raw`T_E`}/>.</Typography>
              <Typography className={classes.topicoS1}>E  <span className={classes.emphasis}>filho</span> de B.</Typography>
              <Typography className={classes.topicoS1}>E  <span className={classes.emphasis}>neto</span> de A.</Typography>
            </Box>
          </Box>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores: Exerccios">
        <List component="ol" className={classes.numbersOl}>
          <Topico><span style={{fontSize: '0.8em'}}>Implementar uma rvore com as operaes de buscar, adicionar, excluir e print.</span></Topico>
          <Topico><span style={{fontSize: '0.8em'}}>Ao buscar um n, retornar o nvel em que o n se encontra. Se ele no existir na rvore, retorne -1.</span></Topico>
          <Topico><span style={{fontSize: '0.8em'}}>Ao adicionar um n, deve ser informado o seu valor e o valor do n pai dele.</span></Topico>
          <Topico><span style={{fontSize: '0.8em'}}>Ao excluir um n, escolha aleatoriamente um de seus filhos para ser a raiz da subrvore.</span></Topico>
          <Topico><span style={{fontSize: '0.8em'}}>O mtodo print deve atender  seguinte relao de recorrncia: <span style={{fontSize: '0.8em'}}><Math tex={String.raw`
              print(no) \begin{cases}
                \varnothing &\text{, se o n  uma subrvore vazia} \\
                (no.valor\text{ }(print(no.filhos[0]),\text{ }print(no.filhos[1]),\text{ }...,\text{ }print(no.filhos[m]))) &\text{, para todo n m-rio}
            \end{cases}`}/></span></span></Topico>
        </List> 
      </ImdSlide>
      <ImdSlide heading="rvore Binria: Definio">
        <ArvoreBinariaDefinicao/>
      </ImdSlide>
      <ImdSlide heading="rvore Binria">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria arvore={arvoreBinaria}/>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvore Estritamente Binria">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria arvore={arvoreEstritamenteBinaria} estritamenteBinaria={true}/>
          <Typography className={classes.topicoS2}>Todos os ns possuem <span className={classes.emphasis}><Math tex={String.raw`grau == 0`}/></span> ou <span className={classes.emphasis}><Math tex={String.raw`grau == 2`}/></span>.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvore Binria Completa">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria arvore={arvoreBinariaCompleta} binariaCompleta={true}/>
          <Typography className={classes.topicoS2}>Se um n possui <span className={classes.emphasis}><Math tex={String.raw`grau < 2`}/></span>, ento ele est no <span className={classes.emphasis}>ltimo ou penltimo nvel</span> de T.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvore Binria Cheia">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria arvore={arvoreBinaria} binariaCompleta={true}/>
          <Typography className={classes.topicoS2}>Se um n possui <span className={classes.emphasis}><Math tex={String.raw`grau < 2`}/></span>, ento ele est no <span className={classes.emphasis}>ltimo nvel</span> de T.</Typography>
          <Typography className={classes.topicoS2}><span className={classes.danger}>Esta rvore  estritamente binria? Ela  completa?</span></Typography>
        </Box>
      </ImdSlide>
      <ImdSlideDuvidas/>
      <ImdSlide heading="Referncias">
        <Box className={classes.slideContentColumnBox}>
          <List component="ul" className={classes.numbersOl}>
            <Topico>SZWARCFITER, Jayme Luiz; MARKENZON, Lilian. Estruturas de dados e seus algoritmos. 3. ed. Rio de Janeiro: LTC, 2010.</Topico>
          </List>
        </Box>
      </ImdSlide>
    </>
  );
}
