import '../../../App.css';
import SlideInicial from '../../../ImdCommons/SlideInicial';
import { List, makeStyles } from '@material-ui/core';
import { themeBaseStyles } from '../../../styles';
import SlideAgenda, { Topico } from '../../../ImdCommons/SlideAgenda';
import ImdSlide from '../../../ImdCommons/ImdSlide';
import { useEffect } from 'react';
import { HerancaDiagramasDeClasse } from './Heranca';

const useStyles = makeStyles((theme, props) => (themeBaseStyles(theme, props)));

export default function SlidesAula05LP2(props) {
  const classes = useStyles(props);

  useEffect(() => {
    document.title = "LP2 - Aula 05 - Herana e Polimorfismo"
  }, []);

  return (
    <>
      <SlideInicial
        codigoDisciplina="IMD0040"
        nomeDisciplina="Linguagem de Programao 2"
        semestre="2020.2"
        />
      <SlideAgenda>
        <Topico>Herana</Topico>
        <Topico>Polimorfismo</Topico>
      </SlideAgenda>
      <ImdSlide heading="Herana">
        <List component="ul" className={classes.bullets}>
          <Topico className={classes.topicoS1}><span className={classes.emphasis}>Herana</span>  um mecanismo que permite que uma classe possa herdar o comportamento e as caractersticas de outra classe.</Topico>
          <Topico className={classes.topicoS1}>ao mesmo tempo que <span className={classes.emphasis}>novos comportamentos e caractersticas</span> podem ser estabelecidos.</Topico>
          <Topico className={classes.topicoS1}>Podemos dizer que uma subclasse <span className={classes.emphasis}>herda</span> de sua superclasse.</Topico>
          <Topico className={classes.topicoS1}>A subclasse  mais <span className={classes.emphasis}>especfica</span> que a superclasse. Por isso, chamamos de <span className={classes.emphasis}>especializao</span>especializao.</Topico>
        </List>
      </ImdSlide>
      <ImdSlide heading="Herana">
        <HerancaDiagramasDeClasse/>
      </ImdSlide>
    </>
  );
}
