const useStyles = makeStyles((theme, props) => (baseStyles));

function SlidesAula05EDB2(props) {
  const classes = useStyles(props);

  useEffect(() => {
    document.title = "EDB2 - Aula 06 - Complexidade de Algoritmos vs Complexidade de Problemas"
  }, []);

  return (
    <>
      <SlideInicial
        codigoDisciplina="IMD0039"
        nomeDisciplina="Estruturas de Dados Bsicas 2"
        assunto="Complexidade de Algoritmos vs Complexidade de Problemas"
        semestre="2020.2"
        />
      <SlideAgenda>
        <Topico>Limite superior de um problema</Topico>
        <Topico>Limite inferior de um problema</Topico>
        <Topico>rvore de deciso</Topico>
        <Topico>Algoritmo timo</Topico>
      </SlideAgenda>
      <ImdSlide heading="Algoritmos">
        <AlgoritmosSlide/>
      </ImdSlide>
      <ImdSlide heading="Problema da ordenao">
        <ProblemaOrdenacao/>
      </ImdSlide>
      <ImdSlide heading="Problema da ordenao">
        <ProblemaOrdenacao2/>
      </ImdSlide>
      <ImdSlide heading="Limite Inferior">
        <LimiteInferior/>
      </ImdSlide>
      <ImdSlide heading="rvore de Deciso">
        <ArvoreDecisao/>
      </ImdSlide>
      <ImdSlide heading="rvore de Deciso">
        <ComplexidadeArvoreDecisao/>
      </ImdSlide>
      <ImdSlide heading="Algoritmo timo">
        <AlgoritmoOtimo/>
      </ImdSlide>
      <ImdSlideDuvidas/>
      );
    </>
  );
}

export default SlidesAula05EDB2;
