import '../../../App.css';
import SlideInicial from '../../../ImdCommons/SlideInicial';
import { Box, List, makeStyles, Typography } from '@material-ui/core';
import { themeBaseStyles } from '../../../styles';
import ImdSlideDuvidas from '../../../ImdCommons/ImdSlideDuvidas';
import { useEffect } from 'react';
import SlideAgenda, { Topico } from '../../../ImdCommons/SlideAgenda';
import ImdSlide from '../../../ImdCommons/ImdSlide';
import arvoresAvl from '../../../img/avl.PNG';
import avlSimplesDireita from '../../../img/avlSimplesDireita.PNG';
import avlDuplaDireita from '../../../img/avlDuplaDireita.PNG';
import avlSimplesEsquerda from '../../../img/avlSimplesEsquerda.PNG';
import avlDuplaEsquerda from '../../../img/avlDuplaEsquerda.PNG';
import Math from '../../../ImdCommons/Math';

const useStyles = makeStyles((theme, props) => (themeBaseStyles(theme, props)));

export function SlidesAula12EDB2(props) {
  const classes = useStyles(props);
  const orderedVector = [{value: 8}, {value: 12}, {value: 15}, {value: 18}, {value: 28}, {value: 45}, {value: 62}];

  useEffect(() => {
    document.title = "EDB2 - Aula 12 - rvore AVL"
  }, []);

  return (
    <>
      <SlideInicial
        codigoDisciplina="IMD0039"
        nomeDisciplina="Estruturas de Dados Bsicas 2"
        assunto="rvores AVL"
        semestre="2020.2"
        />
      <SlideAgenda>
        <Topico>rvore AVL</Topico>
        <Topico>Rotaes</Topico>
        <Topico>Demonstrao</Topico>
      </SlideAgenda>
      <ImdSlide heading="rvores AVL">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="left"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>Uma rvore binria T  denominada AVL quando, para qualquer n de T, as alturas de suas duas subrvores, esquerda e direita, diferem em mdulo de at uma unidade. Nesse caso, v  um n regulado.</Typography>
          <Typography className={classes.topicoS2}>Em contrapartida, um n que no satisfaa essa condio de altura  denominado desregulado, e uma rvore que contenha um n nessas condies  tambm chamada desregulada.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores AVL: exemplo">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <img src={arvoresAvl} alt="rvore AVL e no AVL"/>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores AVL: rotaes">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>Rotao simples  direita.</Typography>
          <Typography className={classes.topicoS2}>Rotao simples  esquerda.</Typography>
          <Typography className={classes.topicoS2}>Rotao dupla  direita.</Typography>
          <Typography className={classes.topicoS2}>Rotao dupla  esquerda.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores AVL: rotao simples  direita">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <img src={avlSimplesDireita} alt="rvore AVL: rotao simples  direita"/>
          <Typography className={classes.topicoS2}><Math tex={String.raw`h_E(p) > h_D(p)\:\&\&\:h_E(u) > h_D(u)`}/></Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores AVL: rotao dupla  direita">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <img src={avlDuplaDireita} alt="rvore AVL: rotao dupla  direita"/>
          <Typography className={classes.topicoS2}><Math tex={String.raw`h_E(p) > h_D(p)\:\&\&\:h_E(u) < h_D(u)`}/></Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores AVL: rotao simples  esquerda">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <img src={avlSimplesEsquerda} alt="rvore AVL: rotao simples  esquerda"/>
          <Typography className={classes.topicoS2}><Math tex={String.raw`h_E(p) < h_D(p)\:\&\&\:h_E(u) < h_D(u)`}/></Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores AVL: rotao dupla  esquerda">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <img src={avlDuplaEsquerda} alt="rvore AVL: rotao dupla  esquerda"/>
          <Typography className={classes.topicoS2}><Math tex={String.raw`h_E(p) < h_D(p)\:\&\&\:h_E(u) > h_D(u)`}/></Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvores AVL: demonstrao">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}><a href="https://www.inf.ufsc.br/~aldo.vw/estruturas/simulador/AVL.html" target="_blank" rel="noreferrer">https://www.inf.ufsc.br/~aldo.vw/estruturas/simulador/AVL.html</a></Typography>
        </Box>
      </ImdSlide>
      
      <ImdSlideDuvidas/>
      
      <ImdSlide heading="Referncias">
        <Box className={classes.slideContentColumnBox}>
          <List component="ul" className={classes.numbersOl}>
            <Topico>SZWARCFITER, Jayme Luiz; MARKENZON, Lilian. Estruturas de dados e seus algoritmos. 3. ed. Rio de Janeiro: LTC, 2010.</Topico>
          </List>
        </Box>
      </ImdSlide>
    </>
  );
}
