import '../../../App.css';
import SlideInicial from '../../../ImdCommons/SlideInicial';
import { Box, List, makeStyles, Typography } from '@material-ui/core';
import { themeBaseStyles } from '../../../styles';
import ImdSlideDuvidas from '../../../ImdCommons/ImdSlideDuvidas';
import { useEffect } from 'react';
import SlideAgenda, { Topico } from '../../../ImdCommons/SlideAgenda';
import ImdSlide from '../../../ImdCommons/ImdSlide';
import { BuscaBinaria } from '../aula-07/BuscaBinaria';
import { ArvoreBinariaDefinicao, ArvoresBuscaBinaria } from '../aula-07/Arvores';
import Math from '../../../ImdCommons/Math';
import { arvoreBinaria, arvoreBinariaCompleta, arvoreEstritamenteBinaria } from '../../../data/arvoreBinaria';
import ImdCodePane from '../../../ImdCommons/ImdCodePane';
import { buscaOrdemSimetrica, buscaPreOrdem, buscaPosOrdem } from '../../../codes/percursos_arvore_binaria';

const useStyles = makeStyles((theme, props) => (themeBaseStyles(theme, props)));

export function SlidesAula08EDB2(props) {
  const classes = useStyles(props);

  useEffect(() => {
    document.title = "EDB2 - Aula 08 - rvores Binrias"
  }, []);

  return (
    <>
      <SlideInicial
        codigoDisciplina="IMD0039"
        nomeDisciplina="Estruturas de Dados Bsicas 2"
        assunto="rvores Binrias"
        semestre="2020.2"
        />
      <SlideAgenda>
        <Topico>rvore binria</Topico>
        <Topico>Propriedades de uma rvore binria</Topico>
        <Topico>Tipos de rvores binrias</Topico>
        <Topico>Percursos em rvores binrias</Topico>
      </SlideAgenda>
      <ImdSlide heading="Busca Binria">
        <BuscaBinaria/>
      </ImdSlide>
      <ImdSlide heading="rvores">
        <ArvoresBuscaBinaria buscaBinaria={true}/>
      </ImdSlide>
      <ImdSlide heading="rvore Binria: Definio">
        <ArvoreBinariaDefinicao/>
      </ImdSlide>
      <ImdSlide heading="rvore Binria">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria arvore={arvoreBinaria}/>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvore Binria: Subrvores vazias">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria arvoreBinariaSubarvoresVazias={true}/>
          <Typography className={classes.topicoS2}>O nmero de <span className={classes.emphasis}>subrvores</span> vazias de uma rvore binria com <Math tex={String.raw`n`}/> elementos  <span className={classes.emphasis}>sempre igual a <Math tex={String.raw`n+1`}/></span>.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvore Binria: Subrvores vazias">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria arvoreBinariaSubarvoresVazias2={true}/>
          <Typography className={classes.topicoS2}>Com <span className={classes.emphasis}><Math tex={String.raw`n=6`}/></span>, o nmero de subrvores vazias  <span className={classes.emphasis}><Math tex={String.raw`7`}/></span>.</Typography>
          <Typography className={classes.topicoS2}>E se removermos um n?</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvore Binria: Subrvores vazias">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria arvoreBinariaSubarvoresVazias3={true}/>
          <Typography className={classes.topicoS2}>Com <span className={classes.emphasis}><Math tex={String.raw`n=5`}/></span>, o nmero de subrvores vazias  <span className={classes.emphasis}><Math tex={String.raw`6`}/></span>.</Typography>
          <Typography className={classes.topicoS2}><Math tex={String.raw`n = n-1`}/> e <Math tex={String.raw`T = n-1+2 = n-1`}/></Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvore Estritamente Binria">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria arvore={arvoreEstritamenteBinaria} estritamenteBinaria={true}/>
          <Typography className={classes.topicoS2}>Todos os ns possuem <span className={classes.emphasis}><Math tex={String.raw`grau == 0`}/></span> ou <span className={classes.emphasis}><Math tex={String.raw`grau == 2`}/></span>.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvore Binria Completa">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria arvore={arvoreBinariaCompleta} binariaCompleta={true}/>
          <Typography className={classes.topicoS2}>Se um n possui <span className={classes.emphasis}><Math tex={String.raw`grau < 2`}/></span>, ento ele est no <span className={classes.emphasis}>ltimo ou penltimo nvel</span> de T.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvore Binria Cheia">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria arvore={arvoreBinaria} binariaCompleta={true}/>
          <Typography className={classes.topicoS2}>Se um n possui <span className={classes.emphasis}><Math tex={String.raw`grau < 2`}/></span>, ento ele est no <span className={classes.emphasis}>ltimo nvel</span> de T.</Typography>
          <Typography className={classes.topicoS2}><span className={classes.danger}>Esta rvore  estritamente binria? Ela  completa?</span></Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvore Binria Ziguezague">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria arvoreZiguezague={true}/>
          <Typography className={classes.topicoS2}><span className={classes.emphasis}><Math tex={String.raw`h(T) = n`}/></span></Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvore Binria Cheia: Altura">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria arvore={arvoreBinaria} binariaCompleta={true}/>
          <Typography className={classes.topicoS2}><span className={classes.danger}><Math tex={String.raw`h(T) = ?`}/></span></Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvore Binria Cheia: Altura">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria arvore={arvoreBinaria} binariaCompleta={true}/>
          <Typography className={classes.topicoS2}><span className={classes.emphasis}><Math tex={String.raw`h(T) = 1 + \lfloor \log_2{n} \rfloor`}/></span></Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="rvore Binria: Altura mnima e mxima">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>Altura mnima: <span className={classes.emphasis}><Math tex={String.raw`h(T) = 1 + \lfloor \log_2{n} \rfloor`}/></span></Typography>
          <Typography className={classes.topicoS2}>Altura mxima: <span className={classes.emphasis}><Math tex={String.raw`h(T) = n`}/></span></Typography>
          <Typography className={classes.topicoS2}><span className={classes.emphasis}><Math tex={String.raw`2^{h-1} \leq n \leq 2^h - 1`}/></span></Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="Percurso em rvore Binria">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>Por <span className={classes.emphasis}>percurso</span> entende-se uma visita sistemtica a cada um dos ns de uma rvore.</Typography>
          <Typography className={classes.topicoS2}>Percurso em <span className={classes.emphasis}>pr-ordem</span>.</Typography>
          <Typography className={classes.topicoS2}>Percurso em <span className={classes.emphasis}>ordem simtrica</span>.</Typography>
          <Typography className={classes.topicoS2}>Percurso em <span className={classes.emphasis}>ps-ordem</span>.</Typography>
          <Typography className={classes.topicoS2}>Percurso em <span className={classes.emphasis}>nvel</span>.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="Percurso em pr-ordem">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ImdCodePane language="java">
            {buscaPreOrdem}
          </ImdCodePane>
          <ArvoresBuscaBinaria arvore={arvoreBinaria} withCode={true}/>
        </Box>
      </ImdSlide>
      <ImdSlide heading="Percurso em pr-ordem">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ImdCodePane language="java">
            {buscaPreOrdem}
          </ImdCodePane>
          <ArvoresBuscaBinaria arvore={arvoreBinaria} withCode={true}/>
          <Typography className={classes.topicoS2}><span className={classes.emphasis}>Ordem de percurso:</span> A, B, D, E, C, F, G</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="Percurso em ordem simtrica">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ImdCodePane language="java">
            {buscaOrdemSimetrica}
          </ImdCodePane>
          <ArvoresBuscaBinaria arvore={arvoreBinaria} withCode={true}/>
        </Box>
      </ImdSlide>
      <ImdSlide heading="Percurso em ordem simtrica">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ImdCodePane language="java">
            {buscaOrdemSimetrica}
          </ImdCodePane>
          <ArvoresBuscaBinaria arvore={arvoreBinaria} withCode={true}/>
          <Typography className={classes.topicoS2}><span className={classes.emphasis}>Ordem de percurso:</span> D, B, E, A, F, C, G</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="Percurso em ps-ordem">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ImdCodePane language="java">
            {buscaPosOrdem}
          </ImdCodePane>
          <ArvoresBuscaBinaria arvore={arvoreBinaria} withCode={true}/>
        </Box>
      </ImdSlide>
      <ImdSlide heading="Percurso em ps-ordem">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ImdCodePane language="java">
            {buscaPosOrdem}
          </ImdCodePane>
          <ArvoresBuscaBinaria arvore={arvoreBinaria} withCode={true}/>
          <Typography className={classes.topicoS2}><span className={classes.emphasis}>Ordem de percurso:</span> D, E, B, F, G, C, A</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="Percurso em nvel">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <ArvoresBuscaBinaria arvore={arvoreBinaria} nivel={true}/>
          <Typography className={classes.topicoS2}><span className={classes.emphasis}>Ordem de percurso:</span> A, B, C, D, E, F, G</Typography>
        </Box>
      </ImdSlide>
      <ImdSlide heading="Exerccio">
        <Box display="flex"
            className={classes.root}
            flexDirection="column"
            alignItems="center"
            justifyContent="space-evenly">
          <Typography className={classes.topicoS2}>Modificar a rvore implementada na aula passada para que ela seja binria.</Typography>
          <Typography className={classes.topicoS2}>Implementar os mtodos de busca utilizando os percursos em <span className={classes.emphasis}>pr-ordem</span>, <span className={classes.emphasis}>ordem simtrica</span>, <span className={classes.emphasis}>ps-ordem</span> e em <span className={classes.emphasis}>nvel</span>.</Typography>
          <Typography className={classes.topicoS2}>Implementar os mtodos de impresso dos elementos da rvore em <span className={classes.emphasis}>pr-ordem</span>, <span className={classes.emphasis}>ordem simtrica</span>, <span className={classes.emphasis}>ps-ordem</span> e em <span className={classes.emphasis}>nvel</span>.</Typography>
        </Box>
      </ImdSlide>
      <ImdSlideDuvidas/>
      <ImdSlide heading="Referncias">
        <Box className={classes.slideContentColumnBox}>
          <List component="ul" className={classes.numbersOl}>
            <Topico>SZWARCFITER, Jayme Luiz; MARKENZON, Lilian. Estruturas de dados e seus algoritmos. 3. ed. Rio de Janeiro: LTC, 2010.</Topico>
          </List>
        </Box>
      </ImdSlide>
    </>
  );
}
